import React from 'react';

const appStyle = {
	textAlign: "center",
	fontFamily: 'Hind, sans-serif',
	position: 'fixed',
	width: '100%'
}

const headerStyle = {
	backgroundColor: '#4fd8ff',
	height: '1000px',
}

//const colorBlue = {
//	color: '#4fd8ff',
//}

const headerText = {
	margin: '0px',
}
		
const Header = props => (
	<div style={appStyle}>
		<div style={headerStyle}>
			<h1 style={headerText}>Front-end React Developer</h1>
			<p style={headerText}>I build websites using React.js</p>
		</div>
	</div>
);

export default Header;